body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App {
  font-family: 'Josefin Sans', sans-serif;
  background-color: #e1e1e1;
}

.App-logo {
  display: inline-block;
  background-size: 200px;
  height: 200px;
}
.App-header {
  text-align: center;
  background-color: #380a15;
  height: 275px;
  padding: 10px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}
.rt-td {
  font-weight: bold
}

.player-names {
  font-weight: bold
}

[data-success] {
    padding:5px 0;
  }
  [data-tint="1"] {
    background-color: #e7f3e7;
  }
  [data-tint="2"] {
    background-color: #ffe7e7;
  }
  
[data-success="1"]:before {
    content:"\A0";
    display:block;
    padding:2px 0;
    line-height:1px;
    border-top:8px solid #1C1; 
}
[data-success="0"]:before {
    content:"\A0";
    display:block;
    padding:2px 0;
    line-height:1px;
    border-top:8px solid grey; 
}
[data-success="2"]:before {
    content:"\A0";
    display:block;
    padding:2px 0;
    line-height:1px;
    border-top:8px solid #FF0000; 
}
