/* 
to hide the buttonness of the button
*/
.wodar {
  text-decoration: none;
  color: grey;
}
.wodar:hover {
  text-decoration: none;
  color: grey;
}
.move-down {
  margin-bottom: -15px;
  margin-right: -20px;
}
.ugly-input {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: 0;
}
.btn-grey {
  background-color: lightgrey;
}
.big-font {
  font-size: 5vh;
    vertical-align: top;
      margin: .5% .75%;
}
.big-font:hover {
  text-decoration: none;
  position: relative;
}
.division-image {
  height: 100px;
}
.courier {
  font-family: courier;
  font-size: 2.5vh;
}
.checker {
  background-color: yellow;
}
#canvas {
  position: fixed;
  top: 0;
  left: 0;
}
.fix-index {
  z-index: 1;
}
.modal-width {
  min-width: 500px;
}
@media (max-width:767px) {
  .division-image {
    height: 100px;
    width: 70px;
  }
  .App-logo {
    width: 300px;
  }
  .big-font {
    font-size: 5vh;
    margin: 3px;
  }
  .selected {
    box-shadow: 1px 1vh 2vh 0 #333;
  }
  .table-size {
    font-size: 2vh;
  }
  .venue-figures {
    font-size: 1.2vh;
  }
  .modal-width {
    min-width: 1vh;
  }
}


